import { useRef, useState, Fragment } from "react"
import { Toast } from 'primereact/toast';
import Header from "../components/Site/Header";

const Register = () => {

    const toast = useRef(null);

    const login = useRef(null);
    const password = useRef(null);
    const confirmPassword = useRef(null);
    const email = useRef(null);
    const agree = useRef(null);

    const [token, setToken] = useState(false);
    const [titleBlock, setTitleBlock] = useState('REGISTRATION');
    const [description, setDescription] = useState(<Fragment>By clicking register you are agreeing to <u>mySite terms of service</u>. You also agree that you are of legal age, and that gambling is permitted in your jurisdiction.</Fragment>)

    async function registerHandler() {

        if (!login.current.value) {
            toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Username is not filled', life: 5000});
            return;
        }

        if (!password.current.value) {
            toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Password is not filled', life: 5000});
            return;
        } else {
            if (password.current.value.length < 6) {
                toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Password must contain at least 6 characters', life: 5000});
                return;
            }
            if (password.current.value !== confirmPassword.current.value) {
                toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Passwords do not match', life: 5000});
                return;
            }
        }

        if (!email.current.value) {
            toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Email address is not filled', life: 5000});
            return;
        } else {
            if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email.current.value)) {
                toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Invalid email address. E.g. example@email.com', life: 5000});
                return;
            }
        }

        if (!agree.current.checked) {
            toast.current.show({ severity: 'warn', summary: 'Warning Message', detail: 'Terms must be accepted', life: 5000});
            return;
        }

        const params = {
            login: login.current.value,
            password: password.current.value,
            email: email.current.value
        }


        const req = await fetch('/api/auth/register', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(params)
        });
      
        const res = await req.json();

        let myTitle = titleBlock;
        let myDescription = description;
        let myToken = null;

        if (res.ok) {
            localStorage.setItem('Token', JSON.stringify(res.token));
            myToken = res.token;
            myTitle = 'Thank you for registering';
            myDescription = 'Make sure  you write down your password or you may lose your account without this'
        } else {
            toast.current.show({ severity: 'error', summary: 'Warning Message', detail: res.message, life: 5000});
        }

        setDescription(myDescription);
        setTitleBlock(myTitle);
        setToken(myToken);

    }

    return (
        <div className="main">
            <Header data={ token } />
            <div className="login">
                <div className="login_block">
                    <h2 className="login_title">{ titleBlock }</h2>

                    <div className="login_value">

                        <div className="login_item">
                            <p>Username</p>
                            <input ref={ login } id="loginInput" className="login_item_input" type="text" placeholder="press your username...." readOnly={ token } />
                        </div>

                        <div className="login_item">
                            <p>Password</p>
                            <input ref={ password } id="passwordInput" className="login_item_input" type="text" placeholder="press your password...." readOnly={ token } />
                        </div>

                        <div className={"login_item" + (token ? ' --hidden' : "")}>
                            <p>Password confirm</p>
                            <input ref={ confirmPassword } id="passwordConfirmInput" className="login_item_input" type="text" placeholder="confirm your password...." />
                        </div>

                        <div className="login_item">
                            <p>Email address</p>
                            <input ref={ email } id="emailInput" className="login_item_input" type="text" placeholder="press your email...." readOnly={ token } />
                        </div>

                        <div className="login_item">
                            <p className="descriptions">{ description }</p>
                        </div>

                        <div className={"login_item" + (token ? ' --hidden' : "")}>
                            <div className="login_item__element">
                                <input ref={ agree } type="checkbox" id="agreeStatus" />
                                <label htmlFor="agreeStatus">I agree</label>
                            </div>
                        </div>

                    </div>

                    <div className="btn-container--center">
                        <button onClick={ registerHandler } className={"btn btn--center" + (token ? ' --hidden' : "")}>Register</button>
                        <a className={ (!token ? ' --hidden' : "") } href="/"><button className="btn btn--center">PLAY NOW</button></a>
                    </div>

                </div>    
            </div>
            <Toast ref={ toast } />
        </div>
    )
}

export default Register